.element {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  width: 100%;
  height: 100%;
}

.element-preview {
  height: 40%;
  width: 100%;
  background: var(--black);
  color: var(--white);
  text-transform: uppercase;
  font-size: 3em;
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  border-bottom: var(--border-division) solid var(--medium-gray);
}

.element-preview > div {
  padding: var(--half-margin);
  background: var(--dark-gray);
}

.element-bottom {
  width: 100%;
  height: 60%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
}

.element-preview h2 {
  margin: 0;
  padding: 0;
}

.element-static {
  width: 50%;
  max-width: 400px;
  height: 100%;
  background: var(--black);
}

.element-child {
  width: 50%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 0 auto;
      flex: 1 0 auto;
  -ms-flex-flow: column nowrap;
      flex-flow: column nowrap;
  height: 100%;
  overflow-y: auto;
  padding: 0;
  background: var(--black);
  border-left: var(--border-division) solid var(--medium-gray);
}

.element-child .container {
  padding: var(--margin);
}

.element .list {
  -ms-flex: 1 0 auto;
      flex: 1 0 auto;
  height: calc(100% - var(--tasks-height));
  overflow-y: auto;
}



